  height: 20px;
  line-height: 20px;
  text-align: center;
  padding: 0 20px;
  font-size: 18px;
  background: #ff6fa3;
  color: #FFF;
  box-sizing: border-box;
}

.ribbon:before, .ribbon:after {
  position: absolute;
  content: '';
  width: 0px;
  height: 0px;
  z-index: 1;
}

.ribbon:before {
  top: 0;
  left: 0;
  border-width: 10px 0px 10px 8px;
  border-color: transparent transparent transparent #fff;
  border-style: solid;
}

.ribbon:after {
  top: 0;
  right: 0;
  border-width: 10px 8px 10px 0px;
  border-color: transparent #fff transparent transparent;
  border-style: solid;
}
</style>



